import React, { Component } from 'react'
import {
    Form, Input, Tooltip, Icon, Cascader, Select, Row, Col, Checkbox, Button, AutoComplete,
} from 'antd';
import { signUpWithEmailAndPassword, signInWithGoogle, signInWithFacebook } from '../store/actions/authActions'
import { connect } from 'react-redux'
const { Option } = Select;
const AutoCompleteOption = AutoComplete.Option;

class SignupForm extends Component {

    state = {
        confirmDirty: false,
        autoCompleteResult: [],
    };

    handleChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    handleSubmit = (e) => {
        e.preventDefault()
        this.props.signInWithGoogle()
        
        // this.props.signUpWithEmailAndPassword({
        //     'email': this.state.email,
        //     'password': this.state.password
        // })
    }

    compareToFirstPassword = (rule, value, callback) => {
        const form = this.props.form;
        if (value && value !== form.getFieldValue('password')) {
            callback('Passwords do not match.');
        } else {
            callback();
        }
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const { autoCompleteResult } = this.state;

        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 8 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 16 },
            },
        };
        const tailFormItemLayout = {
            wrapperCol: {
                xs: {
                    span: 24,
                    offset: 0,
                },
                sm: {
                    span: 16,
                    offset: 8,
                },
            },
        };
        const prefixSelector = getFieldDecorator('prefix', {
            initialValue: '86',
        })(
            <Select style={{ width: 70 }}>
                <Option value="86">+86</Option>
                <Option value="87">+87</Option>
            </Select>
        );

        const websiteOptions = autoCompleteResult.map(website => (
            <AutoCompleteOption key={website}>{website}</AutoCompleteOption>
        ));

        return (
            <Form onSubmit={this.handleSubmit}>
                <Form.Item
                    {...formItemLayout}
                    label="E-mail"
                >
                    {getFieldDecorator('email', {
                        rules: [{
                            type: 'email', message: 'Please enter a valid email.',
                        }, {
                            required: true, message: 'Please enter an email.',
                        }],
                    })(
                        <Input id="email " onChange={this.handleChange} />
                    )}
                </Form.Item>
                <Form.Item
                    {...formItemLayout}
                    label={(
                        <span>
                            Name
                        </span>
                    )}
                >
                    {getFieldDecorator('name', {
                        rules: [{ required: true, message: 'Please enter your name.', whitespace: true }],
                    })(
                        <Input id="name" onChange={this.handleChange} />
                    )}
                </Form.Item>
                <Form.Item
                    {...formItemLayout}
                    label="Password"
                >
                    {getFieldDecorator('password', {
                        rules: [{
                            required: true, message: 'Please enter a password.',
                        }, {
                            validator: this.validateToNextPassword,
                        }],
                    })(
                        <Input type="password" id="password " onChange={this.handleChange} />
                    )}
                </Form.Item>
                <Form.Item
                    {...formItemLayout}
                    label="Confirm Password"
                >
                    {getFieldDecorator('passwordConfirm', {
                        rules: [{
                            required: true, message: 'Please confirm your password.',
                        }, {
                            validator: this.compareToFirstPassword,
                        }],
                    })(
                        <Input type="password" onBlur={this.handleConfirmBlur} id="passwordConfirm" onChange={this.handleChange} />
                    )}
                </Form.Item>
                <Form.Item
                    {...formItemLayout}
                    label={(
                        <span>
                            Username&nbsp;
                  <Tooltip title="Public Username?">
                                <Icon type="question-circle-o" />
                            </Tooltip>
                        </span>
                    )}
                >
                    {getFieldDecorator('username', {
                        rules: [{ required: true, message: 'Please enter a username.', whitespace: true }],
                    })(
                        <Input id="username " onChange={this.handleChange} />
                    )}
                </Form.Item>
                <Form.Item {...tailFormItemLayout}>
                    {getFieldDecorator('agreement', {
                        valuePropName: 'checked',
                    })(
                        <Checkbox>I have read the <a href="">agreement</a></Checkbox>
                    )}
                </Form.Item>
                <Form.Item {...tailFormItemLayout}>
                    <Button type="primary" htmlType="submit">Register</Button>
                </Form.Item>
            </Form>
        );
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        signUpWithEmailAndPassword: (creds) => dispatch(signUpWithEmailAndPassword(creds)),
        signInWithGoogle: () => dispatch(signInWithGoogle())
    }
}

export default connect(null, mapDispatchToProps)(Form.create()(SignupForm));